<template>
  <div class="message">
		<div v-for='(item,index) in items' v-bind:key='index'>
			<div class='message-item' >
				<img class='message-item-img' :src='item.src'>
				<img class='message-item-img-arrow' src='static/images/message/arrow.png'>
				<div class='message-item-information'>
					<p class='message-item-title'>{{item.name}}</p>
					<div class='message-item-content' v-on:touchstart='d(index)' v-on:touchmove='c' v-on:touchend='c'>
						<span>{{item.message}}{{index}}</span>
					</div>
				</div>
			</div>
		</div>
		<wv-actionsheet type="ios" :actions="actions" cancel-text="取消" v-model="sheetVisible"></wv-actionsheet>
  </div>
</template>

<script>
export default {
  name: 'message',
  data () {
    return {
			title:'消息通知',
			items:[
				{src:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1527566111601&di=9d7dc84a3465edfbad3970623e9fbb41&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2015-01-24%2F092143844.jpg', name:'系统通知', message:'迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼'}
				,{src:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1527566111601&di=9d7dc84a3465edfbad3970623e9fbb41&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2015-01-24%2F092143844.jpg', name:'系统通知', message:'迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦曼迪迦奥特曼'}
				,{src:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1527566111601&di=9d7dc84a3465edfbad3970623e9fbb41&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2015-01-24%2F092143844.jpg', name:'系统通知', message:'迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼'}
				,{src:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1527566111601&di=9d7dc84a3465edfbad3970623e9fbb41&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2015-01-24%2F092143844.jpg', name:'系统通知', message:'迪迦奥特曼迪迦奥特曼迪迦奥特曼迪迦奥特曼'}
			],
			loop:null,
			single:'',
			key:'',
			actions:[
				{
					name:'删除该条',
					method:this.delthis
				},
				{
					name:'删除全部',
					method:this.delall
				}
			],
			sheetVisible:false,
			data:null
    }
  },
	methods:{
		d: function(index){
			clearInterval(this.loop);
			let that = this;
			this.loop = setInterval(function(){
				that.sheetVisible = true;
				that.key = index;
				console.log(this)
			},500)
		},
		c: function(){
			clearInterval(this.loop);
		},
		delthis:function(){//删除单条
			this.items.splice(this.key,1)
		},
		delall:function(){//删除全部
			this.items = [];
		}
	},
	mounted:function(){
		this.$emit('changetitle',this.$data.title)
		console.log(this)
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.message-item-img{
	width:10vw;
	height:10vw;
	border-radius:50%;
	display:inline-block;
	position:relative;
	top:0vw;
}
.message-item-img-arrow{
	width:3vw;
	height:3vw;
	display:inline-block;
	position:relative;
	top:0vw;
	left:0vw;
}
.message-item-content{
	display:inline-block;
	width:70vw;
	background:#fff;
	border-radius:3vw 3vw;
	padding:2vw;
	position:relative;
	top:-5.5vw;
	left:13vw;
}
.message-item{
	position:relative;
	padding:2vw;
	font-size:1vw;
}
.message-item-title{
	position:absolute;
	top:1vw;
	left:15vw;
}
.ceshi-img{
	width:100vw;
	height:100vw;
}
</style>



