import Vue from 'vue'
import Router from 'vue-router'
import tiezi from '@/components/tiezi/tiezi'/*帖子列表*/
import tieziitem from '@/components/tieziitem/tieziitem'/*帖子详情*/
import my from '@/components/my/my'/*个人详情*/
import message from '@/components/message'/*消息通知*/
import mytiezi from '@/components/mytiezi/mytiezi'/*我的帖子*/
import collection from '@/components/collection'/*收藏*/
import praise from '@/components/praise'/*点赞*/
import myReturnCard from '@/components/myReturnCard/myReturnCard'/*回帖*/
import posting from '@/components/posting'/*发帖*/
import postingvoice from '@/components/postingvoice'/*发帖*/
import topicList from '@/components/topicList/topicList'/*话题详情*/
import search from '@/components/search/search'/*搜索*/

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'tiezi',
      component: tiezi
    }, {
      path: '/my',
      name: 'my',
      component: my
    }, {
      path: '/message',
      name: 'message',
      component: message
    }, {
      path: '/mytiezi',
      name: 'mytiezi',
      component: mytiezi
    }, {
      path: '/collection',
      name: 'collection',
      component: collection
    }, {
      path: '/praise',
      name: 'praise',
      component: praise
    }, {
      path: '/myReturnCard',
      name: 'myReturnCard',
      component: myReturnCard
    }, {
      path: '/posting',
      name: 'posting',
      component: posting
    }, {
      path: '/postingvoice',
      name: 'postingvoice',
      component: postingvoice
    }, {
      path: '/tieziitem',
      name: 'tieziitem',
      component: tieziitem
    }, {
      path: '/topicList',
      name: 'topicList',
      component: topicList
    }, {
      path: '/search',
      name: 'search',
      component: search
    }
  ]
})



